









































h1 {
  font-weight: 900;
}
figure {
  display: block;
  max-width: 100vw;
  margin: 0 !important;
}
img {
  display: block;
  width: auto;
  height: auto;
  max-width: 100%;
  margin: 0 auto;
}
section {
  img {
    opacity: 0;
    transition: opacity .4s .1s ease-out;
    .-mounted & {
      opacity: 1;
    }
  }
}
